<template>

  <md-card class="md-elevation-5 my-hover">

    <div class="md-layout md-gutter md-alignment-top-center">

      <div class="md-layout-item md-size-40 md-large-size-40  md-medium-size-40 md-small-size-40 md-xsmall-size-40">
        <md-card-content>
          <img :src="user_info.photoUrl" class="want my-hover" id="head_img" style="width: 100%;text-align: left"/>
          <p style="border-left: 10px solid rgba(135,206,250,0.6)">👆=头像</p>
        </md-card-content>
      </div>


      <div class="md-layout-item md-size-60 md-large-size-60 md-medium-size-60 md-small-size-100 md-xsmall-size-100"
           v-if="$store.state.isLogin">
        <md-card-content>
          <p style="border-left: 10px solid rgba(135,206,250,0.6)">昵称：{{user_info.userName}}</p>
          <p style="border-left: 10px solid rgba(10,10,0,0.7)">性别：{{user_info.sex==0?'女':'男'}}</p>
          <p style="border-left: 10px solid rgba(135,206,250,0.6)">签名：{{user_info.autograph}}</p>
          <p style="border-left: 10px solid rgba(10,10,0,0.7)">年龄：{{user_info.age}}</p>
          <p style="border-left: 10px solid rgba(135,206,250,0.6)">我的小钱包：{{myMoney}}元</p>
        </md-card-content>

        <md-card-actions>
          <md-button class="my-raised my-hover" to="/edit">编辑信息</md-button>
          <md-button class="my-raised my-hover" to="/mymoney">小钱包包</md-button>
          <md-button class="my-raised my-hover" v-on:click="logout">登出</md-button>
        </md-card-actions>
      </div>
      <div v-else>
        <p style="border-left: 10px solid rgba(135,206,250,0.6)">昵称：{{user_info.userName}}</p>
        <p style="border-left: 10px solid rgba(10,10,0,0.7)">性别：{{user_info.sex==0?'女':'男'}}</p>
        <p style="border-left: 10px solid rgba(135,206,250,0.6)">签名：{{user_info.autograph}}</p>
        <p style="border-left: 10px solid rgba(10,10,0,0.7)">年龄：{{user_info.age}}</p>
        <p style="border-left: 10px solid rgba(135,206,250,0.6)">我的金额：0元</p>
        <p style="color: blue;font-size: 15px;border-left: 10px solid rgba(10,10,0,0.7)">点击下方登录或注册叭</p>
        <md-button class="my-raised my-hover" to="/login">登陆/注册</md-button>

      </div>

    </div>
  </md-card>
</template>

<script>
  export default {
    name: "UserCard",
    data() {
      return {
        user_info: {
          age: "未知",
          autograph: "未知",
          bankCardNumber: null,
          identityCard: null,
          name: null,
          photoUrl: '/static/default.jpg',
          questionResult: null,
          sex: null,
          showMe: "你好神秘吖",
          userId: null,
          userName: '神秘人请登录',
        },
        myMoney: 0
      }
    },
    methods: {
      getMe() {
        let _this = this
        this.$myapi.get("/user_info/me", null, function (res) {
          _this.user_info = res.data
        })
      },
      logout() {
        sessionStorage.clear()
        this.$store.commit("logout")
      },
      show_buy_amount() {
        let _this = this
        _this.$loading.show()
        this.$myapi.get("/buy_order/show/buy_amount", null, function (res) {
          _this.myMoney = res.data.getAllAmount
          _this.$loading.hide()
        })
      }
    },
    created() {
      if (this.$store.state.isLogin) {
        this.getMe()
        this.show_buy_amount()
      }
    }
  }

</script>

<style scoped>

</style>



