<template>
  <div>

    <div class="md-layout md-gutter md-alignment-top-center">
      <div class="md-layout-item md-size-30 md-large-size-30 md-medium-size-30 md-small-size-100 md-xsmall-size-100">
        <FundCard/>
      </div>

      <div
        class="md-layout-item md-large-size-50 md-medium-size-40 md-small-size-100 md-xsmall-size-100">
        <FundInfoCard/>
      </div>
    </div>
  </div>

</template>

<script>
  import FundCard from "../cards/FundCard"
  import FundInfoCard from "../cards/FundInfoCard"
  import SmallPerson from './SmallPerson'

  export default {
    name: "Fund",
    components: {
      FundCard,
      FundInfoCard,
      SmallPerson
    },
    data() {
      return {
        words: ['<b>不要看人家的详细资料嘛</b>','<b>持仓表......?.......详细信息？.......估值表???</b>']
      }
    },
    created() {
      this.$store.commit("setWords", this.words)
    }
  }
</script>

<style scoped>

</style>



