<template>
  <md-dialog :md-active.sync="$store.state.msg.isLoadDialog" class="md-elevation-5 my-hover">
    <md-dialog-content>
      <p>
        (｡･∀･)ﾉﾞ嗨
      </p>
      <p style="color: aquamarine" v-if="$store.state.msg.code==100">
        {{$store.state.msg.content}}
      </p>
      <div style="color: red" v-else-if="$store.state.msg.code==401">
        <p>{{$store.state.msg.content}}</p>
        <span><a style="color: aquamarine;text-decoration-line: underline" v-on:click="go_to_login">点我登录</a></span>
      </div>
      <p v-else style="color:red;">
        {{$store.state.msg.content}}
      </p>
      <md-card-actions>
        <md-button v-on:click="$store.commit('closeDialog')" class="my-raised my-hover">好哒</md-button>
      </md-card-actions>
    </md-dialog-content>
  </md-dialog>
</template>

<script>
  export default {
    name: "DialogCard",
    methods:{
      go_to_login(){
        this.$router.push('/login')
        this.$store.commit("closeDialog")
      }
    }
  }
</script>

<style scoped>

</style>



