<template>
  <div>
    <md-dialog :md-active.sync="buy_flag" class="md-elevation-5 my-hover">
      <md-dialog-content>
        <img :src="buy_info.photo" style="width: 400px"/>
        <p style="border-left: 10px solid rgba(10,10,0,0.7)">购买基金名称：<b>{{buy_info.fundName}}</b>({{buy_info.fundId}})
        </p>
        <p style="border-left: 10px solid rgba(10,10,0,0.7)">申购费率：<b>{{buy_info.buyingRate*100}}%</b></p>
        <p style="border-left: 10px solid rgba(135,206,250,0.6)">手续费：<b>{{(buy_info.buyingRate*buy_form.transaction_amount).toFixed(2)}}元</b>
        </p>
        <p style="border-left: 10px solid rgba(135,206,250,0.6)"><b>净值：预计按照第{{getWhatDay()}}天15:00来算</b>
          <small>(不含今天)</small>
        </p>
        <p style="border-left: 10px solid rgba(10,10,0,0.7)">买入时间：<b>{{buy_info.thistime}}</b></p>

        <md-field>
          <label>买入金额（1元起）</label>
          <md-input v-model="buy_form.transaction_amount"></md-input>
        </md-field>
        <md-field>
          <label>输入支付密码</label>
          <md-input type="password" v-model="buy_form.pay_password"></md-input>
        </md-field>
        <md-card-actions>
          <md-button class="my-raised my-hover" v-on:click="buy_flag=false">不发电啦！</md-button>
          <md-button class="my-raised my-hover" v-on:click="confirm_buy">发电！</md-button>
        </md-card-actions>

      </md-dialog-content>
    </md-dialog>
    <!--基金模块-->
    <div class="md-layout md-gutter md-alignment-top-center">

      <!--用户信息模块-->
      <div class="md-layout-item md-large-size-30 md-medium-size-40 md-small-size-100 md-xsmall-size-100">
        <UserCard class="my-hover"/>
      </div>

      <!--推荐基金表-->
      <div
        class="md-layout-item md-large-size-50 md-medium-size-40 md-small-size-100 md-xsmall-size-100">
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-large-size-50 md-medium-size-50 md-small-size-100 md-xsmall-size-100"
               v-for="(fund,index) in funds">
            <md-card class="my-hover">
              <md-card-content>
                <img :src="fund.photo" style="height: 200px"/>
                <p style="border-left: 10px solid rgba(135,206,250,0.6)">基金名称：<b style="color: black;">{{fund.fundName}}</b></p>
                <p style="border-left: 10px solid rgba(10,10,0,0.7)">
                  标签：<span v-for="type in fund.type"><b style="color: black"><span v-if="type=='1'">动漫</span>
              <span v-if="type=='2'">游戏</span>
              <span v-if="type=='3'">其他</span></b>
                </span>
                </p>
                <p style="border-left: 10px solid rgba(135,206,250,0.6)">
                  基金经理：<a style="text-decoration: underline;color: black" v-for="(name,index) in fund.managerNames"
                          v-on:click="$router.push('/manager/' + fund.managerIds[index])">{{name}}   </a>
                </p>

              </md-card-content>
              <md-card-actions>
                <md-button :to="'/fund/' + fund.fundId" class="my-raised">查看详情</md-button>
                <md-button class="my-raised" v-on:click="buy(index)">为爱发电</md-button>

              </md-card-actions>
            </md-card>

          </div>
        </div>
        <!--<md-card class="my-hover">-->
        <md-card-actions>
          <md-button class="my-raised" to="/all-fund">查看更多二次元基金</md-button>
        </md-card-actions>
        <!--</md-card>-->
      </div>


    </div>
  </div>
</template>

<script>
  import SmallPerson from './SmallPerson'
  import Register from './Register'
  import UserCard from '../cards/UserCard'

  export default {
    name: 'Index',
    components: {
      UserCard,
      SmallPerson,
      Register
    },
    watch: {
      funds() {
      }
    },
    data() {
      return {
        // 小人物说的话
        words: ['<b style="color:red;">这个上面是推荐的关于二次元基金哟！</b>'],
        funds: [],
        // 购买基金flag
        buy_flag: false,
        buy_info: [],
        buy_form: {
          fund_id: null,
          transaction_amount: 1,
          pay_password: null
        }
      }
    },
    methods: {
      // pull和build项目
      pullPriority() {
        let _this = this;
        // _this.$loading.show()
        this.$myapi.get('/fund/priority', null, function (res) {
          _this.funds = res.data;
          for (let i = 0; i < _this.funds.length; i++) {
            _this.funds[i].managerNames = _this.funds[i].managerNames.split("-")
            _this.funds[i].managerIds = _this.funds[i].managerIds.split("-")
          }
        })
        // _this.$loading.hide()
      },
      buy(index) {
        this.buy_flag = true;
        this.buy_info = this.funds[index];
        this.buy_form.fund_id = this.buy_info.fundId;
        this.buy_info['thistime'] = this.getDate()
      },
      confirm_buy() {
        this.buy_flag = false;
        let _this = this;
        this.$myapi.post("/buy_order/add", _this.buy_form, function (res) {
          _this.$alert.show(100, "购买成功👌，金额为" + _this.buy_form.transaction_amount + "，基金产品名为" + _this.buy_form.fund_id);
          _this.buy_form.transaction_amount = 1;
          _this.buy_form.pay_password = null
        })
      },
      getWhatDay() {
        let date = new Date();
        let m = parseInt(date.getHours());
        let whatDay = 0;
        let day = parseInt(date.getDay());
        if (day >= 1 && day <= 4) {
          whatDay += m < 15 ? 0 : 1
        } else {
          if (day == 5) {
            whatDay += m < 15 ? 0 : 3
          } else {
            whatDay += day == 0 ? 1 : 2
          }
        }
        return whatDay
      },
      getDate() {
        let date = new Date();
        let seperator1 = "-";
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let currentdate = year + seperator1 + month + seperator1 + strDate + ' ' + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
        return currentdate;
      }
    },
    created() {
      this.pullPriority()
      this.$store.commit("setWords", this.words)
      // this.isLoginIn()
      // this.init_weibo()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }

  .my-hover:hover {
    cursor: url('https://www.mmgal.com/wp-content/themes/mygalgame/ui/images/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .md-card {
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 5px;
  }


</style>



