import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Edit from '@/components/Edit'
import Fund from '@/components/Fund'
import Login from '@/components/Login'
import Rank from '@/components/Rank'
import MyMoney from '@/components/MyMoney'
import AllFund from '@/components/AllFund'
import StopStockTable from '@/components/StopStockTable'
import RegisterMore from '@/components/RegisterMore'
import Manager from '@/components/Manager'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/edit',
      name: 'Edit',
      component: Edit
    },
    {
      path: '/register-more',
      name: 'RegisterMore',
      component: RegisterMore
    },
    {
      path: '/fund/:id',
      name: 'Fund',
      component: Fund
    },
    {
      path: '/manager/:id',
      name: 'Manager',
      component: Manager
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/rank',
      name: 'Rank',
      component: Rank
    },
    {
      path: '/mymoney',
      name: 'MyMoney',
      component: MyMoney
    },
    {
      path: '/all-fund',
      name: 'AllFund',
      component: AllFund
    },
    {
      path: '/rank',
      name: 'Rank',
      component: Rank
    },
    {
      path: '/stop-stock',
      name: 'StopStock',
      component: StopStockTable
    },
  ]
})



