<template>
  <div>
    <!--<SmallPerson :words="words" :duration="10000"/>-->


    <md-dialog :md-active.sync="see_flag" :style="$store.state.isAndroid==true?'width:100%':'width:50%'"
               class="md-elevation-5 my-hover">
      <md-dialog-content>
        <md-card>
          <template v-for="(fund,index) in funds">
            <md-card-content class="change-color">
              <md-avatar>
                <img :src="fund.photo">
              </md-avatar>
                <h3><b>{{fund.fundName}}</b></h3>
                <span><b>基金编号：</b> {{fund.fundId}} </span>
              <b>基金经理：</b> <a style="text-decoration: underline" v-for="(name,index) in fund.managerNames"
                              v-on:click="$router.push('/manager/' + fund.managerIds[index])">{{name}}  </a>
              <md-card-actions>
                <md-button :to="'/fund/'+fund.fundId" class="my-raised">查看详情</md-button>
              </md-card-actions>
            </md-card-content>

          </template>
        </md-card>


      </md-dialog-content>
    </md-dialog>
    <!--基金模块-->
    <div class="md-layout md-gutter md-alignment-top-center">


      <!--推荐基金表-->
      <div
        class="md-layout-item md-large-size-50 md-medium-size-50 md-small-size-100 md-xsmall-size-100">
        <div class="md-layout md-gutter">

          <div class="md-layout-item md-large-size-100 md-medium-size-100 md-small-size-100 md-xsmall-size-100">
            <md-card class="my-hover">
              <md-card-content>

                <p style="color: blue;font-size: 25px;">停停停....停牌表↓</p>


                <md-list class="md-triple-line" style="padding: 0;background-color: white">
                  <template v-for="(stock,index) in stocks">
                    <md-list-item class="change-color">

                      <div class="md-list-item-text">
                        <h3><b>{{stock.name}}</b>
                          <small>（股票名字哟）</small>
                        </h3>
                        <span><b>股票编号：</b> {{stock.stockId}} </span>
                      </div>
                      <md-card-actions>
                        <md-button class="my-raised" v-on:click="see(stock.stockId)">查看包含该股票基金</md-button>
                      </md-card-actions>
                    </md-list-item>

                    <md-divider class="md-inset"></md-divider>
                  </template>
                </md-list>
                <md-card-actions>
                  <md-field>
                    <label>{{page}}页/{{end_page}}页</label>
                    <md-input v-model="page" type="number"></md-input>

                  </md-field>
                  <md-button class="my-hover my-raised" v-on:click="findByPageName(page,searchdb)">搜索</md-button>
                  <md-button class="my-hover my-raised" v-if="page!=1" v-on:click="pre">
                    上一页.
                  </md-button>
                  <md-button class="my-hover my-raised" v-if="page!=end_page" v-on:click="next">
                    下一页.
                  </md-button>
                </md-card-actions>
              </md-card-content>

            </md-card>
          </div>

        </div>
        <!--<md-card class="my-hover">-->
        <!--</md-card>-->
      </div>


    </div>
  </div>
</template>

<script>
  import SmallPerson from './SmallPerson'
  import Register from './Register'
  import UserCard from '../cards/UserCard'

  export default {
    name: 'Index',
    components: {
      UserCard,
      SmallPerson,
      Register
    },
    data() {
      return {
        searchdb: null,
        // 小人物说的话
        words: ['<b style="color:red;">这个是全部基金哟！</b>', '<b style="color:orange;">不要被软妹币乱了眼吖</b>', '<b style="color:blue;">祝欧(｡･∀･)ﾉﾞ。</b>'],
        stocks: [],
        // 购买基金flag
        see_flag: false,
        buy_info: [],
        buy_money: 0,
        page: 1,
        fundPage: 1,
        funds: [],
        end_page:1
      }
    },
    updated() {
      this.changeColor()
    },
    methods: {
      next(){
        this.page = parseInt(this.page) + 1
        this.pullDisappearStock(this.page)
      },
      pre(){
        this.page = parseInt(this.page) - 1
        this.pullDisappearStock(this.page)

      },
      // pull和build项目
      pullDisappearStock(page) {
        let _this = this
        this.$myapi.get( '/stock/disappear/' + page, {},function (res) {
            _this.stocks = res.data.list
          _this.end_page = res.data.lastPage
        })
      },
      changeColor: function () {
        let oTable = document.getElementsByClassName('change-color')
        for (let i = 0; i < oTable.length; i++) {
          if (i % 2) {
            oTable[i].style.backgroundColor = '#F0F8FF'
          } else {
            oTable[i].style.backgroundColor = 'white'
          }
        }
      },
      see(stockId) {
        let _this = this
        this.$myapi.get( "/fund/disappear/" + stockId + "/" + _this.fundPage,{},function (res) {
          _this.see_flag = true
          _this.funds = res.data.list
          for (let i = 0; i < _this.funds.length; i++) {
            _this.funds[i].managerNames = _this.funds[i].managerNames.split('-')
            _this.funds[i].managerIds = _this.funds[i].managerIds.split('-')

          }
        })
      },
      getDate() {
        let date = new Date();
        let seperator1 = "-";
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let currentdate = year + seperator1 + month + seperator1 + strDate
        return currentdate;
      }
    },
    created() {
      this.pullDisappearStock(1)
      this.$store.commit("setWords", this.words)

      // this.init_weibo()
    },
    mounted() {
      this.changeColor()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }

  .my-hover:hover {
    cursor: url('https://www.mmgal.com/wp-content/themes/mygalgame/ui/images/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .md-card {
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 5px;
  }


</style>



