export default {
  state: {
    /**
     *用来储存用户的基本信息
     */
    info: {
      age: "小鲜肉",
      autograph: "这个人很懒",
      bankCardNumber: null,
      identityCard: null,
      name: null,
      photoUrl: 'http://wx3.sinaimg.cn/mw690/006okS32ly8fla2yv1mowj30dz0e8t9n.jpg',
      questionResult: null,
      sex: null,
      showMe: false,
      userId: null,
      userName: '尚未登陆的迷失者'
    },
    isGetInfo:false,
  },
  getters: {
  },
  mutations:{
    /**
     * 设置userInfo
     * @param state
     * @param userInfo
     */
    setUserInfo: function (state, userInfo) {
      state.info = userInfo
      state.isGetInfo = true
    },
    /**
     * 重置userInfo
     */
    resetUserInfo: function (state) {
      state.info = {
        age: "小鲜肉",
        autograph: "这个人很懒",
        bankCardNumber: null,
        identityCard: null,
        name: null,
        photoUrl: '/static/default.jpg',
        questionResult: null,
        sex: null,
        showMe: "你好神秘",
        userId: null,
        userName: '尚未登陆迷失者',
      }
      state.isGetInfo = false
    }
  }
}



