<template>
  <!--卡片包含-->
  <md-card class="md-elevation-5 my-hover">
    <md-card-content>
      <!--图片装饰模块-->
      <img src="/static/zc.gif"/>

      <!--显示注册说明模块-->
      <p style="color: blue;font-size: 20px;text-align: right">注册叭↖</p>

      <!--手机号字段-->
      <md-field :class="isPhone(form.phone_number)">
        <label>手机号</label>
        <md-input type="number" v-model="form.phone_number">
        </md-input>
        <md-button class="my-raised my-hover">获取手机验证码</md-button>
        <span class="md-error">这不是一个正确的手机号码</span>
      </md-field>

      <!--手机验证码模块，这里不做字段要求-->
      <md-field>
        <label>手机验证码</label>
        <md-input>
        </md-input>
      </md-field>

      <!--输入密码字段模块-->
      <md-field :class="isNull(form.password)">
        <label>密码</label>
        <md-input type="password" v-model="form.password">
        </md-input>
        <span class="md-error">密码不可以少于6位哦</span>
      </md-field>

    </md-card-content>

    <!--点击按钮注册模块-->
    <md-card-actions>
      <md-button class="my-raised my-hover" style="background-color: white;color: black">转去登录？
      </md-button>
      <md-button class="my-raised my-hover" style="background-color: white;color: black" v-on:click="register">注册
      </md-button>
    </md-card-actions>

  </md-card>
</template>

<script>
  export default {
    name: "Register",
    data() {
      return {
        // 小人物说的话
        words: ['<b style="color:orange">注册享好礼哦，可是什么是好礼呢~~~当然是拥有账号啦</b>'],
        // 注册使用的表单
        form: {
          password: '',
          phoneNumber: '',
        },
      }
    },
    methods: {
      isPhone(phone_number) {
        if (phone_number)
          return this.$myapi.isPhone(phone_number) ? '' : 'md-invalid'
        else return ''
      },
      isNull(password) {
        if (password)
          return this.$myapi.isPassword(password) ? '' : 'md-invalid'
        else return ''
      },
      register() {
        let _this = this;
        if (_this.$myapi.isPhone(_this.form.phone_number) && _this.$myapi.isPassword(_this.form.password)) {
          this.$myapi.post('/user/add', _this.form, function (res) {
            _this.$alert.show(100, "注册成功辣+请填写详细资料哦")
            sessionStorage.setItem("token", res.token)
            _this.$router.push("/register-more")
          })
        } else {
          _this.$alert.show("999", "填写格式错误")
        }
      }
    },
    created() {
      this.$store.commit("setWords", ['<b style="color:black">哼，看什么看！注册叭</b>', '<b style="color: orange">注册完啦我再和你说说话</b>'])
    }
  }
</script>

<style scoped>
  .my-hover:hover {
    cursor: url('https://www.mmgal.com/wp-content/themes/mygalgame/ui/images/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }
</style>



