<template>
  <div>
    <meta content="no-referrer" name="referrer"/>

    <md-dialog :md-active.sync="buy_flag" class="md-elevation-5 my-hover">
      <md-dialog-content>
        <img :src="buy_info.photo" style="width: 400px"/>
        <p style="border-left: 10px solid rgba(10,10,0,0.7)">购买基金名称：<b>{{buy_info.fundName}}</b>({{buy_info.fundId}})
        </p>
        <p style="border-left: 10px solid rgba(10,10,0,0.7)">申购费率：<b>{{buy_info.buyingRate*100}}%</b></p>
        <p style="border-left: 10px solid rgba(135,206,250,0.6)">手续费：<b>{{(buy_info.buyingRate*buy_form.transaction_amount).toFixed(2)}}元</b>
        </p>
        <p style="border-left: 10px solid rgba(135,206,250,0.6)"><b>净值：预计按照第{{getWhatDay()}}天15:00来算</b>
          <small>(不含今天)</small>
        </p>
        <p style="border-left: 10px solid rgba(10,10,0,0.7)">买入时间：<b>{{buy_info.thistime}}</b></p>

        <md-field>
          <label>买入金额（1元起）</label>
          <md-input v-model="buy_form.transaction_amount"></md-input>
        </md-field>
        <md-field>
          <label>输入支付密码</label>
          <md-input type="password" v-model="buy_form.pay_password"></md-input>
        </md-field>
        <md-card-actions>
          <md-button class="my-raised my-hover" v-on:click="buy_flag=false">不发电啦！</md-button>
          <md-button class="my-raised my-hover" v-on:click="confirm_buy">发电！</md-button>
        </md-card-actions>

      </md-dialog-content>
    </md-dialog>
    <!--基金模块-->
    <div class="md-layout md-gutter md-alignment-top-center">


      <!--推荐基金表-->
      <div
        class="md-layout-item md-large-size-60 md-medium-size-60 md-small-size-100 md-xsmall-size-100">
        <div class="md-layout md-gutter">

          <div class="md-layout-item md-large-size-100 md-medium-size-100 md-small-size-100 md-xsmall-size-100">
            <md-card class="my-hover">
              <md-card-content>

                <md-tabs class="md-transparent" md-alignment="fixed">
                  <md-tab id="x" md-label="动漫" v-on:click="type_1_click"></md-tab>
                  <md-tab id="y" md-label="游戏" v-on:click="type_2_click"></md-tab>
                  <md-tab id="z" md-label="其他" v-on:click="type_3_click"></md-tab>
                </md-tabs>


                <div class="md-toolbar-section-end">
                  <md-field md-clearable>
                    <label for="search-jobs-name">
                      基金/股票/基金经理！
                    </label>
                    <md-input id="search-jobs-name" v-model="searchdb"/>
                  </md-field>
                  <md-button class="my-raised md-primary" v-on:click="findByNmae(1,searchdb)">搜索</md-button>
                </div>

                <p style="color: blue;font-size: 25px;">基基基...基金表↓</p>

                <md-list class="md-triple-line" style="padding: 0;background-color: rgba(255,255,255,.9)">
                  <template v-for="(fund,index) in funds">
                    <md-list-item class="change-color">
                      <md-avatar>
                        <img :src="fund.photo">
                      </md-avatar>
                      <div class="md-list-item-text">
                        <h3><b>{{fund.fundName}}</b>
                          <small>（基金名字哟）</small>
                        </h3>
                        <span><b>基金编号：</b> {{fund.fundId}} <b>基金类型</b> <span v-for="type in fund.type"><span
                          v-if="type=='1'">动漫</span>
              <span v-if="type=='2'">游戏</span>
              <span v-if="type=='3'">其他</span>
                </span>  <b>基金经理：</b> <a style="text-decoration: underline" v-for="(name,index) in fund.managerNames"
                                         v-on:click="$router.push('/manager/' + fund.managerIds[index])">{{name}}  </a></span>
                      </div>
                      <md-card-actions>
                        <md-button :to="'/fund/'+fund.fundId" class="my-raised">查看详情</md-button>
                        <md-button class="my-raised" v-on:click="buy(index)">为爱发电</md-button>

                      </md-card-actions>
                    </md-list-item>

                    <md-divider class="md-inset"></md-divider>
                  </template>
                </md-list>
                <md-card-actions>
                  <p style="font-size: 25px">{{page}}/{{end_page}}</p>

                </md-card-actions>

                <md-card-actions>
                  <md-field>
                    <label>{{page}}页/{{end_page}}页</label>
                    <md-input v-model="page" type="number"></md-input>

                  </md-field>
                  <md-button class="my-hover my-raised" v-on:click="findByPageName(page,searchdb)">搜索</md-button>
                  <md-button class="my-hover my-raised" v-if="page!=1" v-on:click="pre">
                    上一页.
                  </md-button>
                  <md-button class="my-hover my-raised" v-if="page!=end_page" v-on:click="next">
                    下一页.
                  </md-button>
                </md-card-actions>
              </md-card-content>

            </md-card>
          </div>

        </div>
        <!--<md-card class="my-hover">-->
        <!--</md-card>-->
      </div>


    </div>
  </div>
</template>

<script>
  import Register from './Register'
  import UserCard from '../cards/UserCard'

  export default {
    name: 'Index',
    components: {
      UserCard,
      Register
    },
    data() {
      return {
        searchdb: null,
        // 小人物说的话
        words: ['<b style="color:red;">这个是全部基金哟！</b>', '<b style="color:orange;">不要被软妹币乱了眼吖</b>', '<b style="color:blue;">祝欧(｡･∀･)ﾉﾞ。</b>'],
        funds: [],
        // 购买基金flag
        buy_flag: false,
        buy_info: [],
        buy_money: 0,
        isLoading: false,
        page: 1,
        end_page: 1,
        type: 1,
        find_flag: false,
        buy_info: [],
        buy_form: {
          fund_id: null,
          transaction_amount: 1,
          pay_password:null
        }
      }
    },
    updated() {
      this.changeColor()
    },
    methods: {
      confirm_buy() {
        this.buy_flag = false
        let _this = this
        this.$myapi.post( "/buy_order/add", _this.buy_form,function (res) {
          _this.$alert.show(100, "购买成功👌，金额为" + _this.buy_form.transaction_amount + "，基金产品名为" + _this.buy_form.fund_id);
          _this.buy_form.transaction_amount = 1;
          _this.buy_form.pay_password = null
        })
      },
      getWhatDay() {
        let date = new Date()
        let m = parseInt(date.getHours())
        let whatDay = 0
        let day = parseInt(date.getDay())
        if (day >= 1 && day <= 4) {
          whatDay += m < 15 ? 0 : 1
        } else {
          if (day == 5) {
            whatDay += m < 15 ? 0 : 3
          } else {
            whatDay += day == 0 ? 1 : 2
          }
        }
        return whatDay
      },
      findByNmae(page, regex) {
        this.find_flag = true
        this.page = 1
        this.findByPageName(page, regex)
      },
      findByPageName(page, regex) {
        let _this = this
        _this.$loading.show()
        this.$myapi.get( '/fund/regex/' + page , {regex:regex},function (res) {
          _this.funds = res.data.list
          _this.end_page = res.data.lastPage
          for (let i = 0; i < _this.funds.length; i++) {
            _this.funds[i].managerNames = _this.funds[i].managerNames.split('-')
          }
          _this.$loading.hide()
        })
      },
      type_1_click() {
        this.page = 1
        this.find_flag = false
        this.type = 1
        this.pullFund(1, this.type)
      },
      type_2_click() {
        this.page = 1
        this.find_flag = false
        this.type = 2
        this.pullFund(1, this.type)
      },
      type_3_click() {
        this.page = 1
        this.find_flag = false
        this.type = 3
        this.pullFund(1, this.type)
      },
      next() {
        this.page = parseInt(this.page) + 1
        this.find_flag == true ? this.findByPageName(this.page, this.searchdb) : this.pullFund(this.page, this.type)
      },
      pre() {
        this.page = parseInt(this.page) - 1
        this.find_flag == true ? this.findByPageName(this.page, this.searchdb) : this.pullFund(this.page, this.type)
      },
      // pull和build项目
      pullFund(page, type) {
        let _this = this
        this.isLoading = true
        this.$myapi.get( '/fund/type/' + page + "/" + type, {},function (res) {
          _this.funds = res.data.list
          _this.end_page = res.data.lastPage
          for (let i = 0; i < _this.funds.length; i++) {
            _this.funds[i].managerNames = _this.funds[i].managerNames.split('-')
            _this.funds[i].managerIds = _this.funds[i].managerIds.split('-')
          }
          _this.isLoading = false
        })
      },
      changeColor: function () {
        let oTable = document.getElementsByClassName('change-color')
        for (let i = 0; i < oTable.length; i++) {
          if (i % 2) {
            oTable[i].style.backgroundColor = '#F0F8FF'
          } else {
            oTable[i].style.backgroundColor = 'rgba(255,255,255,.9)'
          }
        }
      },
      buy(index) {
        this.buy_flag = true
        this.buy_info = this.funds[index]
        this.buy_form.fund_id = this.buy_info.fundId
        this.buy_info['thistime'] = this.getDate()
      },
      getDate() {
        let date = new Date();
        let seperator1 = "-";
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let currentdate = year + seperator1 + month + seperator1 + strDate
        return currentdate;
      }
    },
    created() {
      // this.init_weibo()
      this.$store.commit("setWords",['<b style="color:orange;">看看看！这是全部种类的基金唉！！！！！！！</b>'])
    },
    mounted() {
      this.pullFund(this.page, 1)
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }

  .my-hover:hover {
    cursor: url('https://www.mmgal.com/wp-content/themes/mygalgame/ui/images/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .md-card {
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 5px;
  }


</style>



