<template>
  <div>
    <!--<SmallPerson :words="words" :duration="10000"/>-->

    <div class="md-layout md-gutter md-alignment-top-center">


      <div class="md-layout-item md-large-size-50 md-medium-size-40 md-small-size-100 md-xsmall-size-100">

        <!--<md-dialog :md-active.sync="isLoading">-->
        <!--<md-dialog-content>-->
        <!--<p style="text-align: center">-->
        <!--</p>-->
        <!--<p style="text-align: center">请稍候</p>-->
        <!--<md-progress-bar class="my-raised" md-mode="query"></md-progress-bar>-->
        <!--</md-dialog-content>-->
        <!--</md-dialog>-->

        <md-card class="my-hover">
          <md-card-content id="top">
            <md-tabs class="md-transparent" md-alignment="fixed">
              <md-tab id="x" md-label="用户排行榜" v-on:click="show_user_rank"></md-tab>
              <md-tab id="y" md-label="基金排行榜" v-on:click="show_fund_rank"></md-tab>
            </md-tabs>

            <md-card-actions>
              <md-radio :value="30" @change="get_rank(30,1)" v-model="select_see_date">近30个交易日</md-radio>
              <md-radio @change="get_rank(180,1)" v-model="select_see_date" value="180">近180个交易日</md-radio>
              <md-radio @change="get_rank(365,1)" v-model="select_see_date" value="365">近365个交易日</md-radio>
            </md-card-actions>
            <p style="color: blue;font-size: 25px;" v-show="fund_flag">当当当当前，是第{{page}}页哦</p>


            <div v-show="user_flag">
              <p style="color: blue;font-size: 25px;">为为为防止被掀底裤，用户排名仅显示前十条哦</p>
              <template v-for="(user,index) in users">
                <div class="md-layout md-gutter md-alignment-top-center"
                     style="margin-top: 10px;border-bottom: 3px solid rgba(10,10,0,0.7) ">
                  <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                    <img :src="user.userInfo.photoUrl" style="width: 100%"/>
                  </div>
                  <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                    <p><b>用户名</b>：{{user.userInfo.userName}}</p>
                    <p><b>签名</b>：{{user.userInfo.autograph}}</p>

                  </div>
                  <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                    <p style="color: red"><b>排名</b>：{{index+1}}</p>
                    <p><b>收益率</b>：{{((user.getRate-1)*100).toFixed(2)}}%</p>
                  </div>
                </div>
              </template>


            </div>


            <div v-show="fund_flag">
              <div class="md-layout md-gutter md-alignment-top-center" style="margin-top: 10px;border-bottom: 3px solid rgba(10,10,0,0.7) "
                   v-for="(fund,index) in funds">
                <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                  <img :src="fund.photo" style="width: 100%"/>
                </div>
                <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                  <p><b>基金名：</b>{{fund.fundName}}</p>
                  <p><b>基金id:</b>{{fund.fundId}}</p>

                </div>
                <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                  <p style="color: red"><b>排名</b>：{{index+1}}</p>
                  <p><b>收益率</b>：{{(((parseFloat(fund.historyRate))-1)*100).toFixed(2)}}%</p>
                  <md-button :to="'/fund/'+fund.fundId" class="my-raised">查看详情</md-button>

                </div>
              </div>
              <md-card-actions>
                <md-field>
                  <label>{{page}}页/{{end_page}}页</label>
                  <md-input v-model="page" type="number"></md-input>

                </md-field>
                <md-button class="my-hover my-raised" v-on:click="get_rank(day,this.page)">搜索</md-button>
                <md-button class="my-hover my-raised" v-if="page!=1" v-on:click="pre">
                  上一页.
                </md-button>
                <md-button class="my-hover my-raised" v-if="page!=end_page" v-on:click="next">
                  下一页.
                </md-button>
              </md-card-actions>
            </div>

          </md-card-content>
        </md-card>
      </div>


    </div>
  </div>
</template>

<script>
  import SmallPerson from './SmallPerson'

  export default {
    name: 'HelloWorld',
    components: {
      SmallPerson
    },
    data() {
      return {
        select_see_date: 30,
        user_flag: true,
        fund_flag: false,
        words: ['<b style="color:red;">做人是不可攀比的哦，更不可以嫉妒的吖</b>', '<b style="color:orange;">不要被软妹币乱了眼吖</b>'],
        funds: [],
        page: 1,
        end_page: 1,
        users: []
      }
    },
    methods: {
      next() {
        this.page = parseInt(this.page) + 1
        this.get_rank(this.day, this.page)
      },
      pre() {
        this.page = parseInt(this.page) - 1
        this.get_rank(this.day, this.page)
      },
      show_user_rank() {
        this.user_flag = true
        this.fund_flag = false
        this.select_see_date = 30
        this.get_user_rank(this.select_see_date)
      },
      show_fund_rank() {
        this.user_flag = false
        this.fund_flag = true
        this.select_see_date = 30
        this.get_fund_rank(this.select_see_date, 1)
      },
      get_fund_rank(day, page) {
        let _this = this
        _this.page = page
        _this.day = day
        _this.$myapi.get( '/historical_fund/rank/' + day + '/' + _this.page,{},function (res) {
          _this.funds = res.data.list
          _this.end_page = res.data.lastPage
          _this.$myapi.returnTop("top")
        })
      },
      get_user_rank(day) {
        let _this = this
        _this.day = day
        _this.$myapi.get( '/fund/rank/user/' + day,{},function (res) {
          _this.users = res.data
          _this.end_page = 1
          _this.$myapi.returnTop("top")
        })
      },
      get_rank(day, page) {
        if (this.fund_flag) {
          this.get_fund_rank(day, page)
        } else {
          this.get_user_rank(day)
        }
      }
    },
    created() {
      this.show_user_rank(30)
      this.$store.commit("setWords", this.words)

      // this.init_weibo()
      // this.isLoginIn()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }

  .my-hover:hover {
    cursor: url('https://www.mmgal.com/wp-content/themes/mygalgame/ui/images/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .md-card {
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 3px;
  }


</style>



