<template>
  <div>


    <!--<SmallPerson :words="words" :duration="10000"/>-->
    <md-dialog :md-active.sync="sell_flag" class="md-elevation-5 my-hover">
      <md-dialog-content>
        <!--<img :src="buy_info.url" style="width: 400px"/>-->
        <!--<p style="border-left: 10px solid rgba(10,10,0,0.7)">购买基金名称：{{buy_info.name}}({{buy_info.fund_id}})</p>-->
        <!--<p style="border-left: 10px solid rgba(10,10,0,0.7)">申购费率：{{buy_info.buying_rate*100}}%</p>-->
        <!--<p style="border-left: 10px solid rgba(135,206,250,0.6)">净值：预计按照xx天来算</p>-->
        <!--<p style="border-left: 10px solid rgba(10,10,0,0.7)">买入时间：{{buy_info.thistime}}</p>-->
        <p><b>卖出基金名字：</b>{{sell_form.fundName}}({{sell_form.fundId}})</p>

        <md-field>
          <label>卖出份额(最大{{sell_form.thisFundShare}}份)</label>
          <md-input v-model="sell_form.sellShare"></md-input>
        </md-field>

        <md-field>
          <label>支付密码</label>
          <md-input v-model="sell_form.payPassword"></md-input>
        </md-field>
        <p>
          15点前卖出，将按05月10日(星期五)净值计算金额，年于05月13日(星期一)到账，卖出费率：.00%~1.50%(若持有时长少于7天，基金公司将收取.50%赎回手续费，详见交易规则)
        </p>
        <md-card-actions>
          <md-button class="my-raised my-hover" v-on:click="sell_click">卖出！</md-button>
          <md-button class="my-raised my-hover" v-on:click="sell_flag=false">不卖啦！</md-button>
        </md-card-actions>

      </md-dialog-content>
    </md-dialog>
    <div class="md-layout md-gutter md-alignment-top-center">


      <div class="md-layout-item md-large-size-50 md-medium-size-40 md-small-size-100 md-xsmall-size-100">


        <md-card class="my-hover">
          <md-card-content id="top">

            <p style="font-size: 20px;text-align: center;color: blue">我滴小基金↓</p>
            <!--<p style="font-size: 20px;text-align: center;color: blue" v-if="!$store.state.isLogin">逗你的！没有登录的人没有小基金哦(<a to="/login">点我注册登陆叭</a>)</p>-->

            <div>
              <div class="md-layout md-gutter md-alignment-top-center"
                   style="margin-top: 10px;border-bottom: 2px solid rgba(10,10,0,0.7) ">
                <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30"
                     style="border-right: 2px solid rgba(10,10,0,0.7)">
                  <p>总价值</p>
                  <p style="font-size: 18px;color: black">{{myMoney}}元</p>
                </div>
                <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30"
                     style="border-right: 2px solid rgba(10,10,0,0.7)">
                  <p>昨日收益</p>
                  <p style="font-size: 18px;color: black" >{{yestodayGetMoney.toFixed(2)}}元</p>

                </div>
                <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                  <p>历史收益率</p>
                  <p style="color: red;font-size: 18px">{{(rate*100-100).toFixed(2)}}%</p>
                </div>
              </div>
              <md-tabs class="md-transparent" md-alignment="fixed">
                <md-tab id="x" md-label="我已经购买滴小基金" v-on:click="show_user_rank"></md-tab>

                <md-tab id="y" md-label="交易记录" v-on:click="show_fund_rank"></md-tab>
                1
              </md-tabs>


              <div>

                <md-card-actions v-show="!user_flag">
                  <md-radio :value="true" @change="show_buy(1)" v-model="select_buy_flag">买入</md-radio>
                  <md-radio :value="false" @change="show_sell(1)" v-model="select_buy_flag">卖出</md-radio>
                  <p style="color: red;font-size: 20px;">←</p>
                </md-card-actions>

                <template v-if="!funds.length">
                  <p style="color: blue;font-size: 25px;text-align: right">
                    {{select_buy_flag?"omg,你还没有购买过基金哦":"omg,你还没有卖过基金哦"}}</p>
                </template>
                <template v-else v-for="(fund,index) in funds">
                  <div class="md-layout md-gutter md-alignment-top-center"
                       style="margin-top: 10px;border-bottom: 5px solid rgba(135,206,250,0.6) ">
                    <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                      <img :src="fund.photo" style="width: 100%"/>
                    </div>
                    <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                      <p>基金:<b>{{fund.fundName}}</b></p>
                      <p>基金id:<b>{{fund.fundId}}</b></p>
                      <p v-show="fund.sureNet>0">净值：<b>{{fund.sureNet}}元</b></p>
                      <p v-show="fund.serviceCharge>=0&&fund.sureNet>0">赎回手续费：<b>{{fund.serviceCharge}}元</b></p>
                    </div>
                    <div class="md-layout-item md-large-size-30 md-medium-size-30 md-small-size-30 md-xsmall-size-30">
                      <p v-show="fund.timeBuying">购买时间：<b>{{ $myapi.parseMyDate(fund.timeBuying)}}</b></p>
                      <p v-show="fund.sellTime">卖出时间：<b>{{ $myapi.parseMyDate(fund.sellTime)}}</b></p>
                      <p v-show="fund.transactionAmount">购买金额：<b>{{fund.transactionAmount}}</b>元</p>
                      <p v-show="!user_flag&&!fund.sellTime">状态：<b style="color: red">{{fund.confirmSign?"已成交":"买入待确认净值"}}</b>
                      </p>
                      <p v-show="!user_flag&&fund.sellTime">状态：<b style="color: red">{{fund.confirmSign?"已成交":"卖出待确认净值"}}</b>
                      </p>
                      <p v-show="fund.sellShare&&!user_flag">卖出份额：<b>{{fund.sellShare}}</b></p>

                      <p v-show="fund.allMoney">购买金额：<b>{{fund.allMoney}}</b>元</p>
                      <p style="color: red" v-show="fund.thisFundShare*fund.historyPrice">
                        昨日收益：{{(fund.thisFundShare*fund.historyPrice).toFixed(2)}}元</p>
                      <p style="color: red" v-show="fund.thisFundShare*fund.willMoney">
                        现在总价值：{{(fund.thisFundShare*fund.willMoney).toFixed(2)}}元</p>

                      <md-card-actions>
                        <md-button class="my-raised my-hover" v-on:click="sell(index)" v-show="user_flag">卖出</md-button>
                        <md-button :to="'/fund/' + fund.fundId" class="my-raised">查看详情</md-button>
                      </md-card-actions>

                    </div>
                  </div>
                </template>
                <md-card-actions>
                  <md-field>
                    <label>{{page}}页/{{end_page}}页</label>
                    <md-input v-model="page" type="number"></md-input>

                  </md-field>
                  <md-button class="my-hover my-raised" v-on:click="show_buy(page)">搜索</md-button>
                  <md-button class="my-raised my-hover" v-on:click="pre" v-show="page!=1">上一页</md-button>
                  <md-button class="my-raised my-hover" v-on:click="next" v-show="end_page!=page">下一页</md-button>
                </md-card-actions>

              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>


    </div>
  </div>
</template>

<script>
  import SmallPerson from './SmallPerson'

  export default {
    name: 'HelloWorld',
    components: {
      SmallPerson
    },
    data() {
      return {
        yestodayGetMoney: 0,
        select_buy_flag: true,
        select_see_date: 30,
        user_flag: true,
        fund_flag: false,
        // 卖出弹框
        sell_flag: false,
        words: ['<b style="color: blue">可恶，竟然敢看我的小金库！！！</b>'],
        funds: [],
        page: 1,
        myMoney: 0,
        end_page: 0,
        rate: 0,
        sell_form: {
          sellShare: 0,
          payPassword: null
        }
      }
    },
    methods: {
      // 得到该客户的收益率
      getMyRate() {
        let _this = this;
        _this.$myapi.get("/user_info/me/get_rate", {}, function (res) {
          _this.rate = res.data.rate
        })
      },
      //点击卖出按钮
      sell(index) {
        let _this = this;
        _this.sell_flag = true;
        _this.sell_form = _this.funds[index];
        _this.sell_form['sellShare'] = _this.funds[index].thisFundShare
      },
      //点击确定卖出
      sell_click() {
        let _this = this;
        _this.$myapi.post('/sell_order/add', _this.sell_form, function (res) {
          _this.$alert.show("100", "卖出成功辣");
          _this.show_has_fund(1);
          _this.sell_flag = false
        })
      },
      show_user_rank() {
        this.user_flag = true;
        this.fund_flag = false;
        this.select_buy_flag = false;
        this.show_has_fund(1)
      },
      show_has_fund(page) {
        let _this = this;
        _this.$loading.show();
        this.$myapi.get("/buy_order/show_has_funds/" + page, {}, function (res) {
          if (res.data != null) {
            _this.funds = res.data.list.list;
            _this.yestodayGetMoney = res.data.yestodayGetMoney;
            if(!_this.yestodayGetMoney){
              _this.yestodayGetMoney = 0
            }
            _this.end_page = res.data.list.lastPage;
            _this.$myapi.returnTop("top")
          }
          _this.$loading.hide()
        })
      },
      show_fund_rank() {
        this.select_buy_flag = true;
        this.user_flag = false;
        this.fund_flag = true;
        this.show_buy(1)
      },
      show_buy(page) {
        let _this = this;
        _this.page = page;
        _this.$loading.show();
        this.$myapi.get("/buy_order/show_buy/" + page, {}, function (res) {
          if (res.data != null) {
            _this.funds = res.data.list;
            _this.end_page = res.data.lastPage;
            _this.$myapi.returnTop("top")
          }
          _this.$loading.hide()
        })
      },
      show_sell(page) {
        let _this = this;
        this.$myapi.get("/sell_order/show_sell/" + page, {}, function (res) {
          if (res.data.list != null)
            _this.funds = res.data.list;
          _this.end_page = res.data.lastPage
          _this.$myapi.returnTop("top")
        })
      },
      show_buy_amount() {
        let _this = this;
        _this.$loading.show();
        this.$myapi.get("/buy_order/show/buy_amount", {}, function (res) {
          _this.myMoney = res.data.getAllAmount;
          _this.$loading.hide()
        })
      },
      next() {
        this.page = parseInt(this.page) + 1;
        if (this.fund_flag && this.select_buy_flag) {
          this.show_buy(this.page)
        } else if (this.fund_flag && !this.select_buy_flag) {
          this.show_sell(this.page)
        }
      },
      pre() {
        this.page = parseInt(this.page) - 1;
        if (this.fund_flag && this.select_buy_flag) {
          this.show_buy(this.page)
        } else if (this.fund_flag && !this.select_buy_flag) {
          this.show_sell(this.page)
        }
      }
    },
    created() {
      if (this.$store.state.isLogin) {
        this.show_buy_amount();
        this.show_has_fund(1);
        this.getMyRate()
      }
      this.$store.commit("setWords", this.words)

      // this.show_buy()
      // this.init_weibo()
      // this.isLoginIn()
    },
    mounted() {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }

  .my-hover:hover {
    cursor: url('https://www.mmgal.com/wp-content/themes/mygalgame/ui/images/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .md-card {
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 5px;
  }


</style>



